.rapper img{
    height: 110px;
    margin: 20px auto;
    display: block;
}
.home{
    display: flex;
    list-style: none;
    justify-content: center;
    align-items: center;
    width: 100%;
}
.home li{
    margin: 0 50px;
    text-align: center;
    line-height: 70px;
}

.home li a{
    display: inline-block;
    font-size: 30px;
    text-decoration: none;
    border-radius: 35px;
}

.home li a img{
    width: 150px;
    height: 120px;
}
